.contacts {
	padding: 96px 0 140px;
	@include md {
		padding-bottom: 90px; }
	@include sm {
		padding: 82px 0 53px; }
	&__inner {
		justify-content: space-between;
		flex-wrap: wrap;
		display: flex; }
	&__figures {
		padding: 0 15px 0 9px;
		margin-top: -133px;
		width: 50%;
		@include sm {
			margin-top: 0;
			width: 100%;
			display: flex;
			justify-content: center; }
		img {
			max-width: 100%; } }
	&__figures-desct {
		display: block;
		@include sm {
			display: none; } }
	&__figures-mob {
		display: none;
		@include sm {
			display: block; } }
	&__form {
		margin-top: 62px;
		flex-shrink: 0;
		width: 100%;
		max-width: 365px; } }

