html {
	&.is-open-menu {
		overflow: scroll; } }

body {
	@include f(17px,500,1.176);
	@include fmt;
	@include font-smoothing;
	color: $textColor;
	background-color: $white;
	@include sm {
		font-size: 14px;
		line-height: 1.5; }
	.is-open-menu & {
		position: fixed;
		height: 100vh;
		width: 100%;
		overflow: hidden; } }

.title-lg {
	@include f(45px,600,1.13);
	@include sm {
		@include f(32px,600,1.3125); } }
.title {
	@include f(36px,600,1.22);
	@include sm {
		@include f(24px,600,1.2); } }

.main-wrap {
	position: relative; }

.color-blue {
	color: $blue; }
.color-orange {
	color: #FF5C39; }
