// GENERATE GRID EXAMPLE
// CUSTOM PADDING ON BREAKPOINTS - NOT INCLUDED IN MIXIN
// +generate_grid($columns: 3, $sizeXs: false, $sizeSm: false, $sizeMd: false, $sizeLg: true, $padding: 30px)

@include generate_grid;

.inner {
	margin: 0 auto;
	padding: 0 15px;
	max-width: 1200px;
	@include md {
		padding: 0 30px; } }

.md-hide {
	@include md {
		display: none; } }
.md-show {
	display: none;
	@include md {
		display: block; } }
.sm-hide {
	@include sm {
		display: none !important; } }
.sm-show {
	display: none;
	@include sm {
		display: block; } }
